
import React , {Component, useEffect, useState} from 'react';
import {  Nav  }  from 'react-bootstrap';
import {useHistory, useParams} from 'react-router-dom'



function Detail (props) {


    let [alert, alert변경] = useState(true);
    let [inputData, inputData변경] = useState('');


    
    useEffect(()=>{
      let 타이머timer = setTimeout(()=>{ alert변경(false) }, 2000);

      // return function 어쩌구 (){  실행할코드 }
      // 사라질때 실행이 된다
        return ()=>{ clearTimeout(타이머timer) }
    } , []);

   

    let [누른탭, 탭변경] = useState(0);
    let history = useHistory ();
    let { id } = useParams ();
    let findGood = props.good.find( x => x.id == id);   
    let y = id++; 
    return (
        <div className="container">

            {/* {inputData}
            <input onChange={(e)=>{ inputData변경 (e.target.value)} }/> */}

            {
            alert === true
            ? ( <div className="my-alert2">
                <p> 재고가 얼마 남지 않았습니다.</p>
                </div> )
            : null
            }
                        <div className="row">
            <div className="col-md-6">
                <img src={'https://codingapple1.github.io/shop/shoes'+( y + 1 ) + '.jpg'} width="100%" />
            </div>
            <div className="col-md-6 mt-4">
                <h4 className="pt-5"> {findGood.title} </h4>
                <p>{findGood.content}</p>
                <p>{findGood.price}원</p>
                
                
                <button className="btn btn-danger" onClick = {() => {
                    history.push('/cart' )
                }}
                >주문하기</button> 
                <button className="btn btn-danger" onClick ={()=> {
                    history.push("/")
                }
                }>뒤로가기</button> 
            </div>
            </div>

            <Nav className = "mt-5"  variant="tabs" defaultActiveKey="link-1">
              
                <Nav.Item>
                    <Nav.Link eventKey="link-1" onClick= {()=> { 탭변경(0) }}>Activ</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="link-2" onClick= {()=> { 탭변경(1) }}>Link</Nav.Link>
                </Nav.Item>
                
            </Nav>

            <TabContent 누른탭 = { 누른탭} />
            
        </div> 
    )
};  

function TabContent (props){
 if ( props.누른탭 === 0) {
    return <div> 0번째 내용            </div>
    }
  else if ( props.누른탭 === 1) {
    return <div>          1번째 내용  </div>
  }
  else if ( props.누른탭 === 2) {
    return <div>          2번째 내용  </div>
  }

}   

// props.good[id]
export default Detail;

